$font-primary: system-ui, sans-serif;
$font-secondary: monospace;

$text-margin-primary: 1em 0 0.25em;
$text-margin-tablet: 1em 0 0.25em;
$text-margin-mobile: 1em 0 0.25em;
$text-margin-primary-bottom: 0 0 0.25em;
$text-margin-tablet-bottom: 0 0 0.25em;
$text-margin-mobile-bottom: 0 0 0.25em;
$text-padding-primary: 0;
$text-padding-tablet: 0;
$text-padding-mobile: 0;

$h1-color: $primary-color;
$h1-font-size: 5rem;
$h1-font-weight: 600;
$h1-line-height: 1;
$h1-letter-spacing: -0.01em;
$h1-margin: 0 0.5em 0.5em;
$h1-padding: 0.5em 0.5em 0.5em 0;

$h1-font-size-tablet: 4rem;
$h1-font-weight-tablet: 600;
$h1-line-height-tablet: 1;
$h1-letter-spacing-tablet: -0.01em;
$h1-margin-tablet: 0 0.5em 0.5em;
$h1-padding-tablet: 0.5em 0.5em 0.5em 0;

$h1-font-size-mobile: 5rem;
$h1-font-weight-mobile: 600;
$h1-line-height-mobile: 1;
$h1-letter-spacing-mobile: -0.01em;
$h1-margin-mobile: 0 0.5em 0.5em;
$h1-padding-mobile: 0.5em 0.5em 0.5em 0;

$h2-color: $primary-color;
$h2-font-size: 4.5rem;
$h2-font-weight: 600;
$h2-line-height: 1;
$h2-letter-spacing: -0.01em;
$h2-margin: $text-margin-primary;
$h2-padding: 0;

$h2-font-size-tablet: 4rem;
$h2-font-weight-tablet: 600;
$h2-line-height-tablet: 1;
$h2-letter-spacing-tablet: -0.01em;
