$h2-padding-tablet: 0;

$h2-font-size-mobile: 6rem;
$h2-font-weight-mobile: 600;
$h2-line-height-mobile: 1;
$h2-letter-spacing-mobile: -0.01em;
$h2-margin-mobile: $text-margin-mobile;
$h2-padding-mobile: 0;

$h3-color: $primary-color;
$h3-font-size: 3rem;
